<!--  -->
<template>
  <div class="child_view">
    <playlist :keyWord="keyWord" :playlistData="playlistData"></playlist>
  </div>
</template>

<script>
import { HttpManager } from '../../api';
import Playlist from '../../components/Playlist.vue';

export default {
  name: "SearchPlaylist",
  data () {
    return {
      keyWord: this.$route.query.key,
      playlistData: {}
    };
  },

  components: { Playlist },

  methods: {
    getPlaylist (val) {
      var that = this;
      HttpManager.getSearchByKeyWord("PlayList", {
        key: this.keyWord,
        pn: val,
        rn: 30
      })
        .then((res) => {
          that.playlistData = Object.assign({}, that.playlistData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              playlist: res.data.list,
              pageSize: 30,
              getData: that.getPlaylist,
            })
          // console.log(res.code);
          // that.code = parseInt(res.code)
          // that.total = parseInt(res.data.total);
          // that.playlist = res.data.list;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {},
  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.keyWord = newVal;
        this.getPlaylist(1);
      },
      immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.name {
  margin-top: 16px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
  span {
    cursor: pointer;
  }
}
.count {
  color: #999;
  margin-top: 5px;
  margin-bottom: 26px;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
}
</style>
<style lang="scss">
.playlist {
  .item {
    .cover_out {
      padding-bottom: 100%;
    }
  }
}
</style>



